* {
  box-sizing: border-box;
}

body {
  margin: 0;
  position:relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Caveat, sans-serif;
  font-size: 2.2rem;
  background: url(/dailynote/static/media/cork-board.2541cc28.png);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header{position:relative;height:40px;width:80%;display:flex;justify-content:space-between;align-items:center;padding:0px 3vw;margin:auto;background-image:url(/dailynote/static/media/hip-square.5d5670a5.png)}@media (max-width: 700px){.header{width:97%;padding:0px 5vw}}@media (max-width: 420px){.header{width:95%;padding:0px 7vw}}.header__h1{font-size:2.6rem;text-decoration:underline}.header__aforyzm{margin:auto 20px auto auto}@media (max-width: 460px){.header__aforyzm{display:none}}.header__icon{fill:var(--textColor);width:25px}.header::after,.header::before{content:"";background-image:url(/dailynote/static/media/cork-board.2541cc28.png);display:block;width:30px;height:30px;position:absolute;transform:rotateZ(45deg)}.header::after{right:-15px;top:5px}.header::before{left:-15px;top:5px}

h1{margin:23px 0px}.info{font-family:"Segoe UI", Tahoma, Geneva, Verdana, sans-serif;color:#565656;position:relative;background:url(/dailynote/static/media/hip-square.5d5670a5.png);width:80%;height:90%;margin:1% auto auto auto;padding:25px 0px 25px 25px;box-shadow:3px 3px 6px #565656}@media (max-width: 700px){.info{width:95%;height:95%}}.info__img{max-width:400px;width:100%;margin:15px;float:right}@media (max-width: 1000px){.info__img{max-width:500px;float:unset;display:block;margin:auto}}.info__list li{margin:30px 0px}.info__list--features>li:nth-of-type(1){list-style:"✨"}.info__list--features>li:nth-of-type(2){list-style:"🎨"}.info__list--features>li:nth-of-type(3){list-style:"⚙"}.info__list--features>li:nth-of-type(4){list-style:"🕹"}.info__list--features>li:nth-of-type(5){list-style:"📏"}.info__list--features>li:nth-of-type(6){list-style:"🎹"}.info__list--shortcuts li{margin:10px 0px}.info__textWrap{overflow-y:auto;overflow-x:hidden;width:100%;height:100%}.info__textWrap ::selection{background-color:#8384ab;color:whitesmoke}.info__textWrap::-webkit-scrollbar{background:#dededede;width:5px}.info__textWrap::-webkit-scrollbar-thumb{background:#8384ab}.info__textWrap--link{color:#8384ab;transition:0.2s}.info__textWrap--link:hover{background:#8384ab;color:whitesmoke}.info__textWrap--paragraph{font-size:1.8rem;margin:5px 0px 15px 20px;color:#676767;line-height:27px;max-width:750px}.info__textWrap--h4{font-size:2.1rem;margin:5px 0px}.info__pin{background-image:url(/dailynote/static/media/pushpin.c2aee1fb.png);background-position:left;background-size:cover;width:50px;height:50px;position:absolute;top:0px}.info__pin--left{left:3px}.info__pin--right{right:-15px}

div.ck.ck-reset.ck-editor.ck-rounded-corners>div.ck.ck-editor__main>div{background:url(/dailynote/static/media/hip-square.5d5670a5.png);background-color:var(--backgroundColor);background-blend-mode:multiply;border:none}.note{--themeColor: #abeebb;--backgroundColor: #abeebbaa;width:100%;height:100%;background:url(/dailynote/static/media/hip-square.5d5670a5.png);background-color:var(--backgroundColor);background-blend-mode:multiply;box-shadow:3px 3px 6px rgba(23,23,23,0.15);transition:box-shadow 0.1s linear, transform 0.1s linear, background 0.1s linear}.note::-webkit-scrollbar{display:none}.note--selected{box-shadow:3px 3px 10px rgba(23,23,23,0.6);transform:scale(1.01);z-index:5}.note--show{width:75%;height:75%;display:block;margin:4% auto auto auto;z-index:50;transform:scale(1.1)}@media (max-width: 700px){.note--show{width:85%;height:85%;margin:10% auto auto auto;max-height:unset}}.note__text{width:100%;height:calc(100% - 40px);word-break:break-word;text-overflow:clip;overflow:auto;padding:0px 10px 10px 10px}.note__text ::selection{background-color:var(--themeColor);color:whitesmoke}.note__text::-webkit-scrollbar{display:none}@media (max-width: 700px){.note__text{height:calc(100% - 35px)}}.note__text--edit{padding:0px}.note__text--edit .ck.ck-editor__top.ck-reset_all{position:-webkit-sticky;position:sticky;top:0px;left:0px}.note__text figure{margin:auto}.note__text table{border:2px solid #343434;margin:auto;padding:0px}.note__text thead{font-weight:bolder}.note__text td,.note__text th{border:1px solid #454545;padding:5px}.note__wrapper{background-color:rgba(23,23,23,0);transition:background 0.3s}@media (max-width: 700px){.note__wrapper{width:46%;height:250px;margin:10px auto}}@media (max-width: 420px){.note__wrapper{width:90%;height:230px}}.note__wrapper--open{position:fixed;top:0px;left:0px;z-index:49;width:100%;height:100%;margin:0px;background:rgba(23,23,23,0.5)}@media (max-width: 700px){.note__wrapper--open{width:100%;height:100%}}

.noteHeader{--headerHeight: 30px;width:100%;height:var(--headerHeight);padding:3px;display:flex;align-items:center;background:var(--themeColor)}@media (max-width: 700px){.noteHeader{--headerHeight: 40px}}.noteHeader__pin{left:-2px;bottom:-6px;background:url(/dailynote/static/media/pushpin.c2aee1fb.png) left no-repeat;background-size:cover;width:50px;height:50px;margin:12px auto 0px 0px}.noteHeader__icon{width:20px;height:20px;margin:0px 5px;fill:var(--mainColor);cursor:pointer}.noteHeader__menu{display:flex;align-items:center}.dropDownMenu{list-style:none;height:var(--headerHeight);margin:0px;padding:0px;transition:0.3s;display:flex;align-items:center}.dropDownMenu:hover{z-index:1;background:whitesmoke;border-radius:5px;max-width:700px}@media (max-width: 700px){.dropDownMenu:hover{transform:translateX(0px)}}.dropDownMenu:hover .dropDownMenu__item{max-width:35px;opacity:1}.dropDownMenu__item{margin:0px 4px;cursor:pointer;width:35px;max-height:var(--headerHeight);margin:auto;position:relative;top:1px;transition:0.3s}.dropDownMenu__item:not(:nth-child(4)){opacity:0;max-width:0px}.innerMenu{max-height:var(--headerHeight);list-style:none;padding:2px;margin:0px;border-radius:5px}.innerMenu:hover{box-shadow:3px 3px 8px #34343444, -3px -3px 8px #f5f5f544;max-height:300px;background:#f5f5f5}.innerMenu:hover .innerMenu__item{height:25px;opacity:1;visibility:visible}.innerMenu__item{height:25px;margin:3px auto;border-radius:50%;cursor:pointer;transition:all 0.2s}.innerMenu__item svg{display:block;margin:auto}.innerMenu__item:not(:nth-child(1)){height:0px;opacity:0;visibility:hidden}.innerMenu__item--yellow{width:25px;background-color:#ebcb33}.innerMenu__item--red{width:25px;background-color:#eb4433}.innerMenu__item--green{width:25px;background-color:#74eb74}.innerMenu__item--purple{width:25px;background-color:#8384ab}

.noteWrap{width:100%;height:100%;padding:1vw;display:grid;grid-gap:8px;overflow-y:auto;grid-template-columns:repeat(auto-fill, minmax(48px, 52px))}.noteWrap::-webkit-scrollbar{width:7px}.noteWrap::-webkit-scrollbar-thumb{background-color:#ebcb33}@media (max-width: 700px){.noteWrap{display:flex;flex-wrap:wrap;min-height:calc(100vh - 90px)}}.noteWrap__button{--buttonSize: 55px;display:block;position:fixed;bottom:20px;right:calc((100% - var(--buttonSize)) / 2);width:var(--buttonSize);height:var(--buttonSize);line-height:var(--buttonSize);font-size:5rem;background-color:#f5f5f5;background-image:url(/dailynote/static/media/hip-square.5d5670a5.png);color:var(--mainColor);border:none;cursor:pointer;outline:none;z-index:6;transform:rotateZ(5deg)}.noteWrap__button:hover{transform:scale(1.02) rotateZ(5deg)}.noteAnimation-enter{opacity:0;transform:translateY(20px) scale(0.98)}.noteAnimation-enter.noteAnimation-enter-active{opacity:1;transform:translateY(0px) scale(1);transition:transform 0.3s ease-out, opacity 0.3s ease-out}.noteAnimation--exit{opacity:1}.noteAnimation-exit.noteAnimation-exit-active{opacity:0;transition:0.3s ease-out}

:root{--textColor: #333c44}a{color:#333c44;color:var(--textColor);text-decoration:none}html{font-size:62.5%;color:#333c44;color:var(--textColor);-webkit-animation:fade .6s linear;animation:fade .6s linear}.App{overflow-x:hidden}h1,h2,h3,h4,h5{margin:15px 0px}p{margin:10px 0px}ul{margin:0px 0px}main{height:calc(100vh - 40px);overflow:auto}main::-webkit-scrollbar{display:none}@-webkit-keyframes fade{from{opacity:0}to{opacity:1}}@keyframes fade{from{opacity:0}to{opacity:1}}

